/**
 *
 * Typography
 *
 */
.headline-base,
.sub-heading-base,
.text-primary {
  color: #C8001D;
}

a {
  color: #2B9CD8;
}

a:hover {
  color: #2B9CD8;
}

.modal-body {
  background-image: linear-gradient(180deg, #EFEFEF 25%, #D1D3D4 64%, #D1D3D4 100%);
}

.modal-footer {
  background-color: #d2d2d2;
}

.modal-header .close {
  color: #2B9CD8;
  opacity: 1;
}

.modal-header .close:hover {
  color: #2284b8;
}

/* ----------------------------------------------------------
//  Actions
// ---------------------------------------------------------- */
.cta-banner,
.header-cta-banner {
  display: flex;
  justify-content: center;
}

.cta-banner {
  background-color: #2B9CD8;
  color: #FFF;
}

.cta-banner a {
  color: inherit;
}

.header-cta-banner {
  background-color: #ebebeb;
  color: white;
}

.header-cta-banner a {
  color: #C8001D;
}

.btn {
  border-radius: 1.875em;
}

.btn-primary {
  background-color: #2B9CD8;
  border-color: #2B9CD8;
  color: #FFF;
}

.btn-primary:hover {
  background-color: transparent;
  border-color: #2284b8;
  color: #2B9CD8;
}

.site-header .navbar-toggle {
  color: #C8001D;
}

.site-header .navbar-toggle a {
  color: inherit;
}

/*=========================================
=            Header CTA Banner            =
=========================================*/
.agent {
  background: #5A5A5C;
  display: flex;
  justify-content: center;
  align-items: center;
}

.agent:after {
  border-color: rgba(136, 183, 213, 0);
  border-left-color: #5A5A5C;
}

.agent-icon-fill {
  fill: #FFF;
}

/*=========================================
=            Header CTA Banner            =
=========================================*/
@media only screen and (max-width: 62em) {
  .navbar .flex-nav a {
    font-weight: normal;
  }
  .navbar .flex-nav li a {
    border-top: 1px solid #ebebeb;
  }
  .navbar .flex-nav li a:focus, .navbar .flex-nav li a:active {
    background-color: #2B9CD8;
    border-top-color: #2B9CD8;
    color: #FFF;
    font-weight: bold;
  }
}

.navbar-cart .badge {
  border-color: #2B9CD8;
  color: #C8001D;
}

/**
 *
 * Forms.
 *
 */
.form-control {
  border-radius: 0;
}

.form-control[placeholder] {
  font-style: italic;
}

.email-signup {
  border-color: rgba(255, 255, 255, 0.5);
  text-align: center;
}

.nav-primary,
.footer-nav-primary {
  color: #2B9CD8;
}

.order-status-nav a {
  color: #2B9CD8;
}

.order-status-nav a:hover {
  color: #2B9CD8;
}

