/* ----------------------------------------------------------
//  Hero section
// ---------------------------------------------------------- */
.hero {
  background-image: linear-gradient(0deg, #D1D3D4 0%, #F6F6F6 64%, #D1D3D4 100%);
}

/* ----------------------------------------------------------
//  Hero with images
// ---------------------------------------------------------- */
.hero-image {
  background-color: #0e0e0e;
}

.hero-image-headline__title,
.hero-image-headline__subhead {
  color: #FFF;
}

.hero-cta__telgroup,
.hero-image-cta {
  border-color: #ebebeb;
  border-color: rgba(255, 255, 255, 0.5);
}

.hero-cta__telgroup a,
.hero-image-cta__telgroup a {
  color: #FFF;
}

.hero-image-cta__title {
  color: #999;
}

.hero-image-cta__divider {
  color: #999;
}

/* ----------------------------------------------------------
//  Hero for Landing Pages
// ---------------------------------------------------------- */
.hero-product__price {
  background-color: #2B9CD8;
  color: #FFF;
}

.hero-product__price:after {
  border-color: transparent;
  border-left-color: #2B9CD8;
}

.hero-product__price .filter-price,
.hero-product__price .filter-label,
.hero-product__price .filter-label-inline {
  color: #FFF;
}

.hero-product__features h4 {
  color: #FFF;
}

.hero-product__features ul {
  color: #ebebeb;
}

.hero-promo__text {
  color: #FFF;
}

.hero-cta {
  color: #FFF;
}

.footer-navbar-light {
  background-color: #FFF;
}

.footer-navbar-light a {
  color: #2B9CD8;
}

.footer-navbar-dark {
  background-color: #242424;
  color: #FFF;
}

.footer-navbar-dark a {
  color: #FFF;
}

.section-hr {
  border-bottom: 2px solid #ebebeb;
}

.section-hr-white {
  border-top: 2px solid #FFF;
}

/*=============================================
=            Frontpage            =
=============================================*/
@media only screen and (max-width: 62em) {
  .brand-value {
    border-bottom: 1px solid #ebebeb;
  }
}

.filter {
  color: #5A5A5C;
  background-color: #FFF;
}

.filter-header {
  border-bottom: 2px solid #ebebeb;
}

.filter-price {
  color: #C8001D;
}

.filter-label,
.filter-label-inline {
  color: #5A5A5C;
}

.filter-feature-group .filter-feature:not(:last-of-type) {
  border-right: 2px solid #ebebeb;
}

@media only screen and (min-width: 47.875em) {
  .filter-promoted .filter-footer {
    border-top: 2px solid #ebebeb;
  }
}

/* ----------------------------------------------------------
//  Search pop-up
// ---------------------------------------------------------- */
.block-ac-results .block-content-area {
  background-color: #FFF;
}

.block-ac-results .block-content-area .block-header .block-header__link span {
  border-bottom-color: #2B9CD8;
}

.block-ac-results .block-content-area .block-header .block-header__link i {
  color: #2B9CD8;
}

.block-ac-results .block-content-area .block-header .block-header__link:hover span {
  color: #2B9CD8 !important;
}

.block-ac-results .block-content-area .block-body .block-svg-icon .st10, .block-ac-results .block-content-area .block-body .block-svg-icon .st12, .block-ac-results .block-content-area .block-body .block-svg-icon .st15 {
  stroke: #2B9CD8;
}

.block-ac-results .block-content-area .block-body .block-svg-icon .st11, .block-ac-results .block-content-area .block-body .block-svg-icon .st14 {
  fill: #2B9CD8;
}

.block-ac-results .block-content-area .block-body .block-body__callout {
  color: #2B9CD8;
}

.block-ac-results .block-content-area .block-footer {
  border-color: #2B9CD8;
}

.block-ac-results .block-content-area .block-footer .block-footer__phone {
  color: #2B9CD8;
}

.block-ac-results .block-content-cover {
  background-color: #2B9CD8;
}

.block-ac-results.closed .block-content-wrap .block-content-area {
  background-color: #2B9CD8;
}

/* -------------------------------------------
//  Search results call-in overlay
// -------------------------------------------*/
.call-overlay .modal-content {
  background-image: linear-gradient(0deg, #2B9CD8 43%, #FFF 43%);
}

.call-overlay .modal-header {
  background: url(../img/call-in-popup-agent-min.png) center 18% no-repeat;
}

.call-overlay .modal-body {
  background-image: none;
  background-color: #FFF;
  border-color: #2B9CD8;
}

.call-overlay .modal-footer {
  background-color: #2B9CD8;
  color: #FFF;
}

.call-overlay__label {
  background: #FFF;
  color: #999;
}

.call-overlay__promo {
  color: #2B9CD8;
}

.results-container #filterContainer .filters .filters-wrap .filter-row .filter-title {
  background-color: #ebebeb;
}

.results-container #filterContainer .filters .filters-wrap .filter-row .filter-box {
  border-color: #ebebeb;
}

.results-container #filterContainer .filters .filters-wrap .filter-row .filter-box .DimensionValue input[type=checkbox]:checked + label {
  color: #2B9CD8;
}

.changeLocContents {
  background-color: #5A5A5C;
}

#multipleProvidersAlert strong, #multipleProvidersAlert p {
  color: #2B9CD8;
}

#multipleProvidersAlert div.close-alert {
  color: #2B9CD8;
}

#multipleProvidersAlert div.bg-cover {
  background-color: #2B9CD8;
}

#multipleProvidersAlert div.bg-border {
  border-color: #2B9CD8;
}

.results-container .compareBtnDiv {
  background-color: #ebebeb;
}

.results-container .compareBtnDiv a {
  color: #333;
}

.results-container .compareBtnDiv a .resultsCountAC {
  color: #333;
}

.results-container #filterToggleOn:hover, .results-container #filterToggleOff:hover {
  background-color: #2B9CD8;
  color: #FFF;
}

@media only screen and (min-width: 62em) {
  .results-container #filterToggleOn:hover, .results-container #filterToggleOff:hover {
    background-color: transparent;
    color: #2B9CD8;
  }
}

.results-container #resultsContainer .result .result-wrap #superCol1 .more-details {
  color: #2B9CD8;
}

.results-container #resultsContainer .result .result-wrap #superCol2 #minorCol1 .price-lockup {
  color: #C8001D;
}

.results-container #resultsContainer .result .result-wrap #superCol2 #minorCol1 .btn {
  background-color: #2B9CD8;
  color: #FFF;
}

.results-container #resultsContainer .result .result-wrap #superCol2 #minorCol1 .btn:hover {
  background-color: transparent;
  color: #2B9CD8;
}

.results-container #resultsContainer .result .result-wrap #superCol2 #minorCol1 #callInNumber span, .results-container #resultsContainer .result .result-wrap #superCol2 #minorCol1 #callInNumber strong {
  color: #2B9CD8;
}

.results-container #resultsContainer .result .result-wrap #superCol2 #minorCol2 .meta-wrap:before, .results-container #resultsContainer .result .result-wrap #superCol2 #minorCol2 .meta-wrap p, .results-container #resultsContainer .result .result-wrap #superCol2 #minorCol2 .meta-wrap a {
  color: #2B9CD8;
}

@media only screen and (min-width: 47.875em) {
  .results-container #resultsContainer .result .result-wrap #superCol1 h3 {
    color: #C8001D;
  }
}

.results-container #resultsContainer .result .reveal-panel h3 {
  color: #C8001D;
}

.results-container #resultsContainer .result .reveal-panel .reveal-panel_promo-box p {
  color: #2B9CD8;
  border-color: #2B9CD8;
}

.results-container .results-btm .pagination li a {
  color: #2B9CD8;
}

.results-container .results-btm .pagination li a:hover {
  text-decoration: none;
}

.results-container .results-btm .pagination li.active a {
  color: #FFF;
  background-color: #2B9CD8;
}

.termsCol__promo {
  color: #C8001D;
}

